

.border-bottom-hairline {
	border-bottom: 1px solid #f1f1f1;
}

.border-top-hairline {
	border-top: 1px solid #f1f1f1;
}

.border-top2-hairline {
	border-top: 1px solid #f1f1f1;

	&:nth-last-child(1) {
		border-bottom: 1px solid #f1f1f1;
	}
}

.border--top-grey {
	border-top: 1px solid grey;
}

.border--bottom-grey {
	border-bottom: 1px solid grey;
}

.border--bottom-ltgrey {
	border-bottom: 1px solid lightgrey;
}

.border--top-ltgrey {
	border-top: 1px solid lightgrey;
}

.border--right-ltgrey {
	border-right: 1px solid #f5f5f5;
}

.collapse-first-pad > p {

	&:nth-child(1) {
		padding-top:	0;
		margin-top:		0;
	}
}

.bg--white {

	background: white;
}

.hide {

	display: none;
}

/*.show-on-widest {

	display: none;
	@include widest { display: inherit; }
}*/

.inline-block {
	display: 			inline-block;
}

.nudge--left 	{ padding-left:  $base-horiz-nudge; }
.nudge--right 	{ padding-right: $base-horiz-nudge; }
.pad--left 		{ padding-left:  $base-horiz-pad; }
.pad--right 	{ padding-right: $base-horiz-pad; }
.step--left 	{ padding-left:  $base-horiz-step; }
.step--right 	{ padding-right: $base-horiz-step; }


.space 			{ margin: $base-spacing 0; }
.space--less 	{ margin: $base-spacing-sml 0; }
.space--more 	{ margin: $base-spacing-big 0; }

