
/* ==================================================================
## Config
===================================================================== */

/* sizing relative to containter, ie titlebar, toolbar, or tagline */

$icon-size:						$base-button-height;
$icon-size--sml:				$base-icon-height;
$icon-spacing:					0; /*.5em;*/
$icon-bg-color:					none; /*#f9f9f9;*/
$icon-hover-bg-color:			#e5e5e5;

/* ==================================================================
## Type
===================================================================== */

.icons {

	/* apply global icon sizing, change to px to make size non-responsive */
	font-size:	1rem;

	&__container {

		/* handle item overflow here*/
	}

		&__item {

			float:						right; /* fill container right to left */
		}
}

			.icon { /*a*/

				display: 				inline-block; /* height/width only work on a-link if inline-block */

				height:					$icon-size;
				line-height: 			$icon-size;
				width:					$icon-size;
				margin-left: 			$icon-spacing;

				text-align:				center; /* horiz align img */
				background: 			$icon-bg-color;
				
				&:hover {
					background: 		$icon-hover-bg-color;
				}

				/*a {
					display: 				inline-block;

					height:					$icon-size;
					line-height: 			$icon-size;
					width:					$icon-size;
					margin-left: 			$icon-spacing;

					text-align:				center; 
					background: 			$icon-bg-color;
					
					&:hover {
						background: 		$icon-hover-bg-color;
					}
				}*/
				
				i {
						
					color: #333;
						/*vertical-align:  	middle;*/

					&:hover{
						color: black;
					}
				}
			}

			.icon--img {

				img {
					vertical-align: 	middle; /* vertical align img */
					/*height: 100%*/

				}
			}

			.icon--fa {

				i {
					color: #333;
					/*vertical-align:  	middle;*/

					&:hover{
						color: black;
					}
				}
			}

			.icon--mat {

				i {

					/*font-size: 24px;*/
					vertical-align: middle;
					line-height: inherit; /* override material-icons line-height that bumps vertical alignment down */
				}
			}	

			.icon--rss {

				font-size: .8em;
			}

			/*.icon--img {}
			.icon--mat {}
			.icon--fa {}*/

			.icon--collapsed {

					height:					$icon-size--sml;
					line-height: 			$icon-size--sml;
					width:					$icon-size;	
			}

			.icon--sml {
				font-size: .9em;
				height:					$base-button-height-sml;
				line-height: 			$base-button-height-sml;
				width:					$base-button-height-sml;
			}

			.icon--smlr {
				font-size: .8em;
			}

			.icon--big {
				font-size: 1.1em;
			}

			.icon--bigr {
				font-size: 1.3em;
			}

			/* titlebox icons use this for now */
			.icon--fit {
				
				img {
					height: 100%;
					width: auto;
				}
			}

			.icon--dropdown {

				position: absolute;
				top:0;
				left:0;
			}


/* ==================================================================
## icon gallery
===================================================================== */

$icongal_base_font_size:	.65rem;
$icongal_button_height:		$base_row_height;
$icongal_icon_size:			1.1em;

.icongallery-wrapper {
	display: inline-block;
	width: 100%;
	vertical-align: middle;
}

.icongallery {

	font-size:		$icongal_base_font_size;

	&__container{

		@include layout((
	        gutter-position: 	before,
	        columns: 			4,
	        gutters: 			0,
	        global-box-sizing: 	border-box
    	));
	}

	&__item {

		@include 		gallery(1 of 4);
		line-height: 	$icongal_button_height;

		> a {	

			display: 				inline-block;
			height:					100%;
			width:					100%;
			text-align:				center; /* horiz align img */
			background: 			$icon-bg-color;
			
			&:hover {
				background: 		$icon-hover-bg-color;
			}

			> img {
				vertical-align: 	middle; /* vertical align img */
			}

			> i {
				font-size:			$icongal_icon_size;
				vertical-align:  	middle;
			}
		}	
	}
}

