/* ==================================================================
## defaults
===================================================================== */
$background-color: #fff;

$base-font-family: Helvetica, Arial, sans-serif;





/* ==================================================================
## Colors
===================================================================== */

/* constants */
$base-font-color: 			#333; /*#505050;*/
$base-font-color-inv: 		#eee; /*#505050;*/
$lighter-font-color: 		#777;
$darker-font-color: 		#333;
$darker-font-color: 		#000;
$openorange-color: 			#ff9d00;
$opengrey-color: 			#888;
$link-font-color: 			#333; /*#777;*/ /*#98a6bc;*/

/* config */
$pagetitle-font-color: 		$darker-font-color;
$blocktitle-font-color: 	$darker-font-color;

/* config */
/* base */
$base-title-font-color: 	$darker-font-color;
$base-body-font-color: 		$base-font-color;
$base-tag-font-color: red;
$base-more-font-color: red;
$base-cell-font-color: red;
$base-icon-font-color: red;


/* slideshow */

$slide-icon-font-color: red;

/* manifesto */
$statement-title-font-color: red;
$statement-body-font-color: red;
$statement-more-font-color: red;

/* recent news */


$title-font-color: 			$darker-font-color;
$menu-font-color: 			$base-font-color;
$manifesto-font-color: 		$lighter-font-color;
$headline-font-color: 		$lighter-font-color;

$tags-font-color: 			#666;
$ts-font-color: 			#999;
$foot-font-color: 			$base-font-color;


$title-link-color: 			$title-font-color;

$menu-link-color: 			$menu-font-color;

$footer-bg-color: 			#333;



$table-title-font-color: 	$darker-font-color;
$table-font-color: 			$darker-font-color;


$manifesto-title-font-color: $darker-font-color;

$link-background-color:		#e5e5e5;


/* ==================================================================
## Fonts Size
===================================================================== */

$base-h1-font-size:			1.2em; /*	sitename			*/
$base-h2-font-size:			1.9em; /*	node titles 		*/
$base-h3-font-size:			1.1em; /*	secondary titles 	*/
$base-h4-font-size:			0.9em; /*	factsheet headers & headlines	*/
$base-h5-font-size:			0.8em; /*	factsheet multiline headers (ie. roles)		*/
$base-h6-font-size:			0.8em; /*	factsheet facts		*/

/* base */
$base-font-size: 			.9em;
$base-title-font-size: 		.9em;
$base-body-font-size: 		red;
$base-tag-font-size: 		red;
$base-more-font-size: 		red;
$base-cell-font-size: 		red;
$base-icon-font-size: 		red;

$sitename-font-size: 15px;
$icon-font-size: 14px;
$pagetitle-font-size: 12px;
$blocktitle-font-size: 12px;
/*$facttitle-font-size: 11px;*/
$title-font-size: 14px;
$jobtitle-font-size: 12px;

$events-headline-font-size: 12px;
$menu-font-size: 12px;
$manifesto-font-size: 1.0em;

$ts-font-size: 10px;




$table-title-font-size: 12px;


$manifesto-title-font-size: 14px;

$toolbar-font-size: 12px;


/* ==================================================================
## Fonts Line Heights
===================================================================== */
$base-line-height: 1.5;
$base-title-line-height: 1.25; /* 1.25 x 3 = 3.75 */

$sitename-line-height: 16px;
$pagetitle-line-height: base-line-height;
$blocktitle-line-height: base-line-height;
$table-title-line-height: 12px;
$title-line-height: 12px;

$events-headline-line-height: 11px;

$ts-line-height: 7px;
$manifesto-line-height: 1.5;
$info-line-height: 14px;

$slide-caption-line-height: $base-line-height;

$manifesto-title-line-height: $base-title-line-height;

/* ==================================================================
## Vertical Spacing
===================================================================== */

$base-row1-height: 		5.25rem; /* Site Header */
$base-row2-height: 		3.75rem; /* Card Headers/Footers */ /* (16px x .7em) x 3.75 = 42px; */
$base-row3-height: 		2.75rem; /* Table Rows */
$base-button-height:	2.75rem; /* Icons, Tags, Buttons */
$base-button-height-sml: 2.25rem; /* Icons, Tags, Buttons */
$base-icon-height:		1rem; /* Icons, Tags, Buttons */
$base-spacing: 			1.25rem;
$base-spacing-sml: 		1.0rem;
$base-spacing-big: 		1.5rem;

/* ==================================================================
## Horiz Spacing
===================================================================== */

$base-horiz-nudge: 		4px;	/* .25em */
$base-horiz-pad: 		8px; 	/* .5em */
$base-horiz-step:		16px; 	/* 1em */



/* degraded */
$base-pad-horiz: 		$base-horiz-nudge;
$base-spacing-horiz: 	$base-horiz-pad;
$base-button-pad:		$base-horiz-step;

$base-spacing-vert: $base-spacing;
$base-row-height: $base-row3-height;
$base-row-pad: 1rem; /*16px;*/
$base-elem-spacing: 1rem; /*16px;*/ /*this should be rem not em AND should be susy calculated gallery gutter*/

/* ==================================================================
## Titles
===================================================================== */

$base-title-height:	$base-row-height;
$base-title-spacing: 1.5rem;

/* ==================================================================
## Buttons (icons, tags, dropdowns)
===================================================================== */


$base-button-pad-horiz:		$base-button-pad;
$base-button-spacing:		0.5em;

/* ==================================================================
## Table
===================================================================== */

$table-row-height: $base-row3-height;
$table-col1-width: 20%;
$table-col2-width: 70%;
$table-col3-width: 10%;

/* ==================================================================
## Grid
===================================================================== */

$total-cols             : 4;  
$col-width              : 162px;  
$gutter-width           : 40px;  
$side-gutter-width      : $gutter-width; 

